@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&display=swap);
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
}

.App {
  margin: 20vh 3.5vw 5vh -2vw;
  font-family: 'Libre Baskerville', serif;
  font-size: 14px;
  display: flex;
}

.App .content-container a {
  text-decoration: none;
  color: black;

  /* For styling underline animation */
  text-decoration: none;
  background-image: linear-gradient(#00000060, #00000060);
  background-position: 0% 100%;
  background-repeat: no-repeat;
  background-size: 100% 1px;
  transition: background-size 0.5s;
}

.App .content-container a:hover {
      /* For styling underline animation */
      background-size: 0% 1px;
}

.navbar-container {
  margin: 0 10px 0 0;
  position: fixed;
}

.content-container {
  margin: 10px 0 0 130px;
  color: black;
}

.content-container p {
  margin-top: 0;
}


/* Start with mobile and increase in size... */
@media only screen and (min-width: 600px) {
  .App {
    margin: 20vh 6vw 5vh 6vw;
  }
}

@media only screen and (min-width: 800px) {
  .App {
    margin: 20vh 16vw 5vh 16vw;
  }
}

@media only screen and (min-width: 1000px) {
  .App {
    margin: 20vh 20vw 5vh 20vw;
  }
}

@media only screen and (min-width: 1200px) {
  .App {
    margin: 20vh 26vw 5vh 26vw;
  }
}

@media only screen and (min-width: 1600px) {
  .App {
    margin: 20vh 32vw 5vh 32vw;
  }
}
.navbar {
    width: 100px;
    margin: 0;
    padding: 5px;
    list-style-type:none;
    font-size: 20px;
    text-align: right;
    letter-spacing:1px;
}

.navbar li {
    margin: 5px 0;
}

/* React injects active class into NavLink */
.active {
    font-style: italic;
    letter-spacing: .1rem;
}

.navlink {
    color: black;
    text-decoration: none;
}

@media only screen and (max-width: 600px) {
  .navbar {
    font-size: 14px;
  }
}
.animation-container {
  position: relative;
  left: 10vw;
  top: -50px;
}


/* Unique objects */
.tree {
  height: 400px;
  width: 300px;
}

.center-dot {
  background: #f1c02f;
  border-radius: 50%;
  position: absolute;
  width: 4px;
  height: 4px;
	-webkit-animation: pulse-double 7s infinite;
	        animation: pulse-double 7s infinite;
}

.plus {
  -webkit-clip-path: polygon(10% 35%, 47% 35%, 47% 0%, 53% 0%, 53% 35%, 90% 35%, 90% 40%, 53% 40%, 53% 80%, 47% 80%, 47% 40%, 10% 40%);
          clip-path: polygon(10% 35%, 47% 35%, 47% 0%, 53% 0%, 53% 35%, 90% 35%, 90% 40%, 53% 40%, 53% 80%, 47% 80%, 47% 40%, 10% 40%);
  height: 14px;
  width: 14px;
  -webkit-animation: opacity 5s infinite;
          animation: opacity 5s infinite;
  -webkit-animation-delay: .5s;
          animation-delay: .5s;
}

.arrow {
  -webkit-clip-path: polygon(50% 0%, 100% 50%, 50% 10%, 0% 50%);
          clip-path: polygon(50% 0%, 100% 50%, 50% 10%, 0% 50%);
  -webkit-animation: opacity 5s infinite;
          animation: opacity 5s infinite;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}


/* Repeating objects */
.rhombus {
  position: absolute;
  -webkit-animation: pulse 7s infinite;
          animation: pulse 7s infinite;
}

.trunk-dot {
  background: #f1c02f;
  position: absolute;
  -webkit-animation: opacity 6s infinite;
          animation: opacity 6s infinite;
}


/* Particles */
.object {
  background: black;
  position: absolute;
}

.square {
  border-radius: 100% 0% 100% 0% / 0% 100% 0% 100%;
  -webkit-animation: opacity 5s infinite;
          animation: opacity 5s infinite;
}

.diamond {
  border-radius: 100% 0% 100% 0% / 0% 100% 0% 100%;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  -webkit-animation: opacity 6s infinite;
          animation: opacity 6s infinite;
}

.dot {
  border-radius: 50%;
  -webkit-animation: opacity 5s infinite;
          animation: opacity 5s infinite;
}

.star {
  -webkit-clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
          clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
  -webkit-animation: opacity 4.5s infinite;
          animation: opacity 4.5s infinite;
}


/* Creating randomness for particles */
.r0 {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

.r1 {
  -webkit-animation-delay: 4s;
          animation-delay: 4s; 
}

.r2 {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}

.r3 {
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
}

.r4 {
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}

.r5 {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}


/* Staggering dots on the tree trunk */
.t0 {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}

.t1 {
  -webkit-animation-delay: .2s;
          animation-delay: .2s;
}

.t2 {
  -webkit-animation-delay: .4s;
          animation-delay: .4s;
}

.t3 {
  -webkit-animation-delay: .6s;
          animation-delay: .6s;
}

.t4 {
  -webkit-animation-delay: .8s;
          animation-delay: .8s;
}


/* Sizes of objects */
.x-small {
  width: 3px;
  height: 3px;
}

.small {
  width: 5px;
  height: 5px;
}

.medium {
  width: 7px;
  height: 7px;
}

.large {
  width: 8px;
  height: 8px;
}

.x-large {
  width: 10px;
  height: 10px;
}


/* Animations */
@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(.95);
            transform: scale(.95);
  }

	50% {
    -webkit-transform: scale(.8);
            transform: scale(.8);
	}

	100% {
    -webkit-transform: scale(.95);
            transform: scale(.95);
  }
}
@keyframes pulse {
  0% {
    -webkit-transform: scale(.95);
            transform: scale(.95);
  }

	50% {
    -webkit-transform: scale(.8);
            transform: scale(.8);
	}

	100% {
    -webkit-transform: scale(.95);
            transform: scale(.95);
  }
}

@-webkit-keyframes pulse-double {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }

	50% {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
	}

	100% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
}

@keyframes pulse-double {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }

	50% {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
	}

	100% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
}

@-webkit-keyframes opacity {
  0% {
    opacity: 1;	}

	70% {
		opacity: 0;
	}

	100% {
		opacity: 1;
  }
}

@keyframes opacity {
  0% {
    opacity: 1;	}

	70% {
		opacity: 0;
	}

	100% {
		opacity: 1;
  }
}


/* Adjust position and scale of tree for screen size */
@media only screen and (max-width: 470px) {
  .animation-container {
    top: -12vh;
    left: -5vw;
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
  }
}

@media only screen and (min-width: 471px) and (max-width: 600px) {
  .animation-container {
    top: -50px;
    left: 4vw;
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
  }
}

@media only screen and (min-width: 1600px) {
  .animation-container {
    left: 7vw;
  }
}
